import React from "react";
import MultiCartItemAttributeComponent from "./MultiCartItemAttributeComponent";

export default class MultiCartItemMainPriceMobileComponent extends MultiCartItemAttributeComponent {
    constructor (props) {
        super(props);
    }

    render() {
        return (
            this.props.price ? (
            <div className="u-table u-left">
                <span className="u-table-cell">
                    <span className="opl-price-v2">
                       <span className="opl-price-v2__part">
                           <span className="opl-price-v2__whole">{ this.getPartsOfPrice(this.props.price)[0] }</span>
                       </span>
                       <span className="opl-price-v2__part">
                           <span className="opl-price-v2__separator">,</span>
                           <span className="opl-price-v2__decimal">{ this.getPartsOfPrice(this.props.price)[1] }</span>
                           <span className="opl-price-v2__suffix">&nbsp;{ this.props.currency }</span>
                       </span>
                    </span>
                </span>
                <span className="u-table-cell u-vertical-top">
                    { this.props.tooltip }
                </span>
            </div>
        ) : null);
    }
}